<template>
  <div>
    <transition name="fade">
      <div class="modal" style="background: rgba(0, 0, 0, 0.5);" v-if="isShow">
        <div class="modal-dialog">
          <div class="modal-content">
            <div class="header">
              <span style="text-align:left">地图掉落</span>
              <toggle-button
                :value="enableEquipmentFilter"
                color="#82C7EB"
                :sync="true"
                :width="lableEquipmentFilter"
                :labels="{unchecked: '不过滤低爆率装备', checked: '过滤低爆率装备'}"
                @change="switchEquipmentFilter"
              />
              <button
                class="close"
                style="background-color:transparent;border:0;"
                v-on:click="close"
              >
                <span>x</span>
              </button>
            </div>
            <div class="item-box" style="overflow:auto;height:400px;">
              <table>
                <tr>
                  <th>地图</th>
                  <th style="text-align:left">掉落一览</th>
                </tr>
                <tr v-for="(item) in mapInfo" :key="item.title">
                  <th>{{item.title}}</th>
                  <td>
                    <div style="display:flex;flex-wrap:nowrap">
                      <div
                        v-for="equipment in item.equipments"
                        :key="equipment.title"
                        style="padding:.5rem;display:flex;flex-direction:column"
                      >
                        <img
                          v-lazy="equipment.src"
                          width="55"
                          v-bind:class="{'map_equipment_no_target': !equipment.isSelect }"
                        />
                        <h6 style="margin:0">{{equipment.priority}}%</h6>
                      </div>
                    </div>
                  </td>
                </tr>
              </table>
            </div>
          </div>
        </div>
      </div>
    </transition>
    <div class="item-box">
      <button class="btn" v-on:click="submit">提交</button>
    </div>
    <div class="item-box">
      <div class="item-title">已选择装备</div>
      <div
        v-if="selectedItems !== undefined && selectedItems !== null && selectedItems.length == 0"
      >请选择装备</div>
      <draggable v-else v-model="selectedItems" class="box">
        <div v-for="(item,index) in selectedItems" :key="item.id" class="item-div">
          <img
            class="item-img equipment"
            :data-index="index"
            :data-select="item.isSelect"
            v-lazy="item.src"
            v-on:click="cancelClick(index)"
          />
        </div>
      </draggable>
    </div>
    <div class="item-box">
      <div class="item-title">装备一览</div>
      <div style="padding:1rem">
        <input class="search-bar" placeholder="輸入裝備名稱搜尋" type="text" v-model="searchInput" />
      </div>
      <div class="box">
        <div class="item-div" v-for="(item,index) in newItems" :key="item.title">
          <img
            class="item-img equipment"
            :data-index="index"
            :data-select="item.isSelect"
            v-lazy="item.src"
            v-on:click="imgClick(index)"
            v-bind:class="{'active': item.isSelect }"
          />
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import draggable from "vuedraggable";
var chineseConv = require("chinese-conv");

export default {
  name: "EquipmentDispaly",
  components: {
    draggable,
  },
  watch: {
    searchInput(newValue, oldValue) {
      if (newValue == oldValue) {
        return;
      } else if (newValue == "") {
        this.items = this.totalItems;
      } else {
        //模糊搜索
        this.items = [];
        let text = chineseConv.tify(newValue);
        this.totalItems.forEach((i) => {
          if (i.title.split(text).length > 1) {
            this.items.push(i);
          }
        });
      }
    },
  },
  data() {
    return {
      totalItems: [], //所有的装备
      items: [], // 显示的装备
      selectedItems: [],
      isShow: false,
      display: "display:none",
      totalMapInfo: [], //总的地图信息
      mapInfo: [], //过滤后的地图信息
      enableEquipmentFilter: true,
      lableEquipmentFilter: 120,
      searchInput: "",
    };
  },
  created: function () {
    this.$http.get("get", (res) => {
      this.items = res.data;
      this.totalItems = this.items;
    });
  },
  methods: {
    filterEquipment() {
      this.mapInfo = [];
      //遍历拿到的地图信息数组
      this.totalMapInfo.forEach((q) => {
        let count = 0;
        q.equipments.forEach((element) => {
          if (element.isSelect) {
            count += element.priority;
          }
          // element.src = "http://127.0.0.1/" + element.title + ".jpg";
          element.src = "https://pcredivewiki.tw/" + element.url;
        });
        if (!this.enableEquipmentFilter || count >= 36) {
          //没有开启过滤就直接加入
          //爆率大于36，说明至少有一件高爆率的装备
          this.mapInfo.push(q);
        }
      });
    },
    // 开关
    switchEquipmentFilter() {
      if (this.enableEquipmentFilter) {
        this.lableEquipmentFilter = 130;
      } else {
        this.lableEquipmentFilter = 120;
      }
      this.enableEquipmentFilter = !this.enableEquipmentFilter;
      this.filterEquipment();
    },
    close() {
      this.isShow = false;
      this.display = "display:none";
      this.mapInfo = [];
    },
    submit() {
      if (this.selectedItems.length === 0) {
        return;
      }
      this.$http.post(
        "sendRI",
        {
          list: this.selectedItems.map((i) => i.title),
        },
        (res) => {
          this.totalMapInfo = res.data;
          if (this.totalMapInfo == null) {
            alert("不存在地图掉落信息");
            return;
          }
          this.filterEquipment();
          this.isShow = true;
          this.display = "display:block";
        },
        (ex) => {
          alert(ex);
        }
      );
    },
    cancelClick(index) {
      this.items.some((e) => {
        if (e.id == this.selectedItems[index].id) {
          e.isSelect = false;
          this.selectedItems.splice(index, 1);
          return true;
        }
      });
    },
    imgClick(index) {
      if (!this.items[index].isSelect) {
        this.selectedItems.push(this.items[index]);
        this.items[index].isSelect = true;
      } else {
        for (var i = this.selectedItems.length - 1; i >= 0; i--) {
          if (this.selectedItems[i].title === this.items[index].title) {
            this.selectedItems.splice(i, 1);
            this.items[index].isSelect = false;
            break;
          }
        }
      }
    },
  },
  computed: {
    newItems() {
      this.items.forEach((element) => {
        // element.src = "http://127.0.0.1/" + element.title + ".jpg";
        element.src = "https://pcredivewiki.tw/" + element.url;
      });
      return this.items;
    },
  },
};
</script>
<style scoped>
@import "../assets/css/EquipmentDisplay.css";
</style>